"use client"

import React, { useRef, useEffect, useMemo, useState } from 'react';
import * as THREE from 'three';
import * as FaIcons from 'react-icons/fa';
import * as SiIcons from 'react-icons/si';
import * as DiIcons from 'react-icons/di';
import * as IoIcons from 'react-icons/io';
import * as TbIcons from 'react-icons/tb';

// Mock data for demonstration
const mockStacks = [
  'React', 'JavaScript', 'TypeScript', 'Node.js', 'Python', 
  'Docker', 'AWS', 'MongoDB', 'PostgreSQL', 'Redux',
  'Next.js', 'Vue.js', 'Angular', 'Express', 'GraphQL'
];

const stackColors = {
  'React': '#61DAFB',
  'JavaScript': '#F7DF1E',
  'TypeScript': '#3178C6',
  'Node.js': '#339933',
  'Python': '#3776AB',
  'Docker': '#2496ED',
  'AWS': '#FF9900',
  'MongoDB': '#47A248',
  'PostgreSQL': '#4169E1',
  'Redux': '#764ABC',
  'Next.js': '#000000',
  'Vue.js': '#4FC08D',
  'Angular': '#DD0031',
  'Express': '#000000',
  'GraphQL': '#E10098'
};

const iconSets = [FaIcons, SiIcons, DiIcons, IoIcons, TbIcons];

function toPascalCase(str) {
  return str
    .replace(/[^a-zA-Z0-9]/g, ' ')
    .replace(/\b\w/g, (l) => l.toUpperCase())
    .replace(/\s+/g, '');
}

function findIconComponent(stack) {
  const candidates = [
    toPascalCase(stack),
    toPascalCase(stack) + 'Js',
    toPascalCase(stack).replace(/Js$/, 'JS'),
    toPascalCase(stack).replace(/Js$/, ''),
  ];

  for (const icons of iconSets) {
    for (const candidate of candidates) {
      for (const prefix of ['Fa', 'Si', 'Di', 'Io', 'Tb']) {
        const iconName = prefix + candidate;
        const iconSet = icons;
        if (iconSet[iconName]) {
          return iconSet[iconName];
        }
      }
    }
  }
  return null;
}

function getStackColor(stack) {
  return stackColors[stack] || '#666666';
}

// Function to create a texture from an icon
function createIconTexture(Icon, color, size = 256) {
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d');
  canvas.width = size;
  canvas.height = size;

  // Clear canvas with transparent background
  ctx.clearRect(0, 0, size, size);

  if (Icon) {
    // Create SVG string
    const svgString = `
      <svg width="${size}" height="${size}" viewBox="0 0 ${size} ${size}" xmlns="http://www.w3.org/2000/svg">
        <foreignObject width="100%" height="100%">
          <div xmlns="http://www.w3.org/1999/xhtml" style="
            width: 100%; 
            height: 100%; 
            display: flex; 
            align-items: center; 
            justify-content: center; 
            font-size: ${size * 0.6}px;
            color: ${color};
          ">
            <div style="transform: scale(1.5);">
              ${Icon.toString().replace(/fill="[^"]*"/g, `fill="${color}"`)}
            </div>
          </div>
        </foreignObject>
      </svg>
    `;

    // Create image from SVG
    const img = new Image();
    const blob = new Blob([svgString], { type: 'image/svg+xml' });
    const url = URL.createObjectURL(blob);
    
    return new Promise((resolve) => {
      img.onload = () => {
        ctx.drawImage(img, 0, 0);
        URL.revokeObjectURL(url);
        
        const texture = new THREE.CanvasTexture(canvas);
        texture.needsUpdate = true;
        resolve(texture);
      };
      img.src = url;
    });
  }

  // Fallback: draw text
  ctx.fillStyle = color;
  ctx.font = `${size * 0.15}px Arial`;
  ctx.textAlign = 'center';
  ctx.textBaseline = 'middle';
  ctx.fillText('?', size / 2, size / 2);

  const texture = new THREE.CanvasTexture(canvas);
  texture.needsUpdate = true;
  return Promise.resolve(texture);
}

function StackBall({ stack, position, index }) {
  const meshRef = useRef();
  const [texture, setTexture] = useState(null);
  
  const Icon = findIconComponent(stack);
  const color = getStackColor(stack);

  useEffect(() => {
    createIconTexture(Icon, color).then(setTexture);
  }, [Icon, color]);

  useEffect(() => {
    if (meshRef.current) {
      // Add floating animation
      const mesh = meshRef.current;
      const startTime = Date.now();
      
      const animate = () => {
        const elapsed = (Date.now() - startTime) / 1000;
        mesh.position.y = position[1] + Math.sin(elapsed + index) * 0.1;
        mesh.rotation.x += 0.005;
        mesh.rotation.y += 0.01;
        requestAnimationFrame(animate);
      };
      animate();
    }
  }, [position, index]);

  const geometry = useMemo(() => new THREE.SphereGeometry(0.8, 32, 32), []);
  
  const material = useMemo(() => {
    const mat = new THREE.MeshPhongMaterial({
      color: '#ffffff',
      shininess: 100,
      transparent: true,
      opacity: 0.9
    });
    
    if (texture) {
      mat.map = texture;
    }
    
    return mat;
  }, [texture]);

  return (
    <mesh ref={meshRef} position={position} geometry={geometry} material={material}>
    </mesh>
  );
}

export default function Stacks3D() {
  const mountRef = useRef(null);
  const sceneRef = useRef(null);
  const rendererRef = useRef(null);
  const cameraRef = useRef(null);
  const frameRef = useRef(null);

  const [loading, setLoading] = useState(true);

  const duplicatedStacks = useMemo(() => {
    return [...mockStacks, ...mockStacks];
  }, []);

  useEffect(() => {
    if (!mountRef.current) return;

    // Scene setup
    const scene = new THREE.Scene();
    scene.background = new THREE.Color('#F5F5F5');
    sceneRef.current = scene;

    // Camera setup
    const camera = new THREE.PerspectiveCamera(
      75,
      mountRef.current.clientWidth / mountRef.current.clientHeight,
      0.1,
      1000
    );
    camera.position.set(0, 0, 15);
    cameraRef.current = camera;

    // Renderer setup
    const renderer = new THREE.WebGLRenderer({ 
      antialias: true,
      alpha: true 
    });
    renderer.setSize(mountRef.current.clientWidth, mountRef.current.clientHeight);
    renderer.shadowMap.enabled = true;
    renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    rendererRef.current = renderer;

    mountRef.current.appendChild(renderer.domElement);

    // Lighting
    const ambientLight = new THREE.AmbientLight('#ffffff', 0.6);
    scene.add(ambientLight);

    const directionalLight = new THREE.DirectionalLight('#ffffff', 0.8);
    directionalLight.position.set(10, 10, 5);
    directionalLight.castShadow = true;
    scene.add(directionalLight);

    const pointLight = new THREE.PointLight('#ffffff', 0.4);
    pointLight.position.set(-10, -10, 10);
    scene.add(pointLight);

    setLoading(false);

    // Handle resize
    const handleResize = () => {
      if (!mountRef.current || !camera || !renderer) return;
      
      camera.aspect = mountRef.current.clientWidth / mountRef.current.clientHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(mountRef.current.clientWidth, mountRef.current.clientHeight);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
      if (frameRef.current) {
        cancelAnimationFrame(frameRef.current);
      }
      if (mountRef.current && renderer.domElement) {
        mountRef.current.removeChild(renderer.domElement);
      }
      renderer.dispose();
    };
  }, []);

  // Animation loop
  useEffect(() => {
    if (!sceneRef.current || !cameraRef.current || !rendererRef.current) return;

    let animationSpeed = 0;
    
    const animate = () => {
      animationSpeed += 0.008;
      
      // Move camera to create marquee effect
      cameraRef.current.position.x = Math.sin(animationSpeed) * 25;
      cameraRef.current.lookAt(0, 0, 0);
      
      rendererRef.current.render(sceneRef.current, cameraRef.current);
      frameRef.current = requestAnimationFrame(animate);
    };
    
    animate();

    return () => {
      if (frameRef.current) {
        cancelAnimationFrame(frameRef.current);
      }
    };
  }, [loading]);

  return (
    <section className="w-full h-[400px] relative overflow-hidden bg-gradient-to-r from-gray-50 via-white to-gray-50">
      <div 
        ref={mountRef} 
        className="w-full h-full"
        style={{ cursor: 'grab' }}
      />
      
      {/* Add balls to the scene */}
      {sceneRef.current && duplicatedStacks.map((stack, index) => {
        const angle = (index / duplicatedStacks.length) * Math.PI * 2;
        const radius = 12;
        const x = Math.cos(angle) * radius;
        const z = Math.sin(angle) * radius;
        const y = (Math.random() - 0.5) * 2;
        
        return (
          <StackBall
            key={`${stack}-${index}`}
            stack={stack}
            position={[x, y, z]}
            index={index}
          />
        );
      })}

      {/* Info overlay */}
      <div className="absolute bottom-4 left-4 text-xs text-gray-500 bg-white/80 px-3 py-2 rounded-lg backdrop-blur-sm">
        <p>ðŸŽ¯ Interactive 3D Stack Showcase</p>
        <p>âœ¨ Each ball represents a technology in our stack</p>
      </div>

      {/* Stack labels overlay */}
      <div className="absolute top-4 right-4 bg-white/90 backdrop-blur-sm rounded-lg p-3 max-w-xs">
        <h3 className="font-semibold text-sm mb-2">Technologies</h3>
        <div className="flex flex-wrap gap-1">
          {mockStacks.slice(0, 8).map((stack) => (
            <span
              key={stack}
              className="text-xs px-2 py-1 rounded-full text-white"
              style={{ backgroundColor: getStackColor(stack) }}
            >
              {stack}
            </span>
          ))}
          <span className="text-xs px-2 py-1 rounded-full bg-gray-400 text-white">
            +{mockStacks.length - 8} more
          </span>
        </div>
      </div>
    </section>
  );
}